	module.exports = {"data":{"markdownRemark":{"html":"<p>整理资料，准备写一篇关于中央权威的文章。《邓小平文选》第三卷中有论述，而且说得透彻。这是近年来出现的新问题。在经济发展和社会发展的过程中，中央权威在各种因素的影响下不如从前。但是，目前事业发展和历史阶段均要求中央有一定权威，没有这样的权威不仅经济发展和社会发展搞不上去，而且政治上也会发生问题。</p>\n<p>邓小平说:\"我的中心意思是，中央要有权威。改革要成功，就必须有领导有秩序地进行。\"\"不能搞'你有政策我有对策'，不能搞违背中央政策的'对策'\"\"党中央、国务院没有权威，局势就控制不住。\"\"中央定了措施，各地各部门就要坚决执行，不但要迅速而且要有力，否则就治理不下来。\"</p>\n<p>中央权威如何来维护，是当前必须讨论的一个问题。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0121.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0121.md","frontmatter":{"title":"7月16日 星期六"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0121.md absPath of file >>> MarkdownRemark"}}

